  --transition--duration: 0.3s;
  border-bottom: 3px solid var(--gp-green-500);
  text-decoration: none;
}
.site-footer ul.list-unstyled.footer-links li a {
  font-family: "Helvetica Now Display Regular", "Helvetica", "Arial", sans-serif;
  font-weight: inherit;
}
.site-footer ul.list-unstyled.footer-links-secondary li a, .site-footer p.copyright-text {
  font-weight: inherit;
  font-size: 0.75rem;
  line-height: 1;
}
